export function rand(number,limit) {
    // console.log(number+"---"+limit);
    var arr = [];
    for(var i=0;i<number;i++){
        var a = Math.ceil(Math.random()*limit);
        var str = arr.toString();
        if(str.indexOf(a.toString())>-1){
            i--;
        }else{
            arr.push(a);
        }
    }
    // console.log(arr);
    return arr;
}

export function sortByReleaseDateDesc(a,b){
    return Date.parse(b.release_date.toString())-Date.parse(a.release_date.toString());
}
export function sortByAddTimeDesc(a,b){
    return b.addTime - a.addTime;
}
export function sortByCreateTimeDesc(a,b){
     return Date(b.createTime.toString())-Date(a.createTime.toString());
 }
export function sortByCreateTimeAsc(a,b){
    return Date(a.createTime.toString())-Date(b.createTime.toString());
}
export function sortByScoreDesc(a,b) {
     return parseFloat(b.score.toString())-parseFloat(a.score.toString());
 }
export function sortByScoreAsc(a,b) {
    return parseFloat(a.score.toString())-parseFloat(b.score.toString());
}
export function sortByTopDesc(a,b) {
    return parseFloat(b.top.toString())-parseFloat(a.top.toString());
}
export function sortByTopAsc(a,b) {
    return parseFloat(a.top.toString())-parseFloat(b.top.toString());
}
export function sortByHitsDesc(a,b) {
    return b.hits-a.hits;
}
export function sortByHitsAsc(a,b) {
    return a.hits-b.hits;
}

