export function changeBaseUrl(callback) {
    var url = baseurl + '/dbs';
    $.ajax({
        url: url,
        crossDomain: true,
        method: 'GET',
        dataType: 'json',
        error: function (err) {
            // if (err)
            //     console.log(err);
            baseurl = baseurl_incase;
            callback();
        },
        success: function (datas) {
            // console.log(datas);
            callback();
        }
    })
}

